import React, { Component } from 'react';
import './editAntd.scss';
import './style.scss';
 import {Table,Tag,Divider,Button,Icon,Row,Col,Card} from 'antd';
 import {createHashHistory} from "history"
 import ReactEcharts from 'echarts-for-react';
import Echarts from 'echarts';
const { Global } = require('../../../../API/Global');
const { shine } = require('../../../../API/shine'); 
const { walden } = require('../../../../API/walden');
const columns = [
    // {

    //     title: '',
    //     align:'center',
    //     width:70, 
    //     key: 'num',
    //     render:(text,record,index)=>`${index+1}`
        
    //     },
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      width:90, 
      align:'center',
      render: text =>  text ,
    },
     
    {
      title: '',
      width:90, 
      dataIndex: 'address',
      key: 'address',
      align:'center',
    },
    {
        title: '',
        dataIndex: 'action',
        width:110,
        align:'center',
        key: 'action',
        render: (text, record) => {
          var color,con ;
          if(text==0){
            color="orange";
            con=""
          }else if(text==1){
              color="green";
              con=""
          }else {
              color="red";
              con=""
          }
          return (
              <span style={{color}}>{con}</span>
          )
        },
      },
    {
      title: '',
      key: 'tags',
      width:100, 
      align:'center',
      dataIndex: 'tags',
      render: tags => (
        <span>
          {tags.map(tag => {
            let color = tag.length > 5 ? 'geekblue' : 'green';
            if (tag === 'loser') {
              color = 'volcano';
            }
            return (
              <Tag color={color} key={tag}>
                {tag.toUpperCase()}
              </Tag>
            );
          })}
        </span>
      ),
    },
   
    {
      title: '',
      width:190, 
      dataIndex: 'caozuo',
      align:'center',
      key: 'caozuo', 
      render: (text, record) => {
        var bom ;
        
        if(text){
            return (
              <span>
                    <Button.Group>
                        <Button size="small" type="primary" ghost>
                        <Icon type="rollback" />
                             
                        </Button>
                        <Button size="small" type="primary" >
                             
                             <Icon type="edit" />
                        </Button>
                    </Button.Group>
              </span>  
            );
        }else {
          return (
                <Button.Group>
                        <Button size="small" type="primary" ghost>
                           <Icon type="upload" />
                              
                        </Button>
                        <Button size="small" type="primary"  ghost>
                              
                              <Icon type="edit" /> 
                        </Button>
                        <Button size="small" type="danger" ghost>
                              
                              <Icon type="delete" /> 
                        </Button>
               </Button.Group>
          )  
        }

        
      }
    }
  ];
  
  const data = [
    {
      key: '1',
      name: '',
      teacher: "",
      action:"1",
      address: '',
      tags: [ ''],
      isClick:1,
      caozuo:1,
    },
    {
      key: '2',
      name: '?',
      teacher: '',
      action:"1",
      address: '',
      tags: [''],
      isClick:1,
      caozuo:1,
    },
    {
      key: '3',
      name: ' ',
      teacher: '',
      action:"0",
      address: '',
      tags: [''],
      isClick:0,
      caozuo:1,
    },
    {
      key: '4',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:0,
      caozuo:0,
    },
    {
      key: '5',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:1,
      caozuo:1,
    },
  ];  
class Me extends Component {
    constructor() {
        super();

        this.state = {
          
        }
    }
    getOption = () => {
        return  {
            title : {
                text: '', 
                x:'center'
            },
            tooltip : {
                trigger: 'item',
                formatter: "{a} <br/>{b} : ({c}) ({d}%)"
            },
            legend: {
                orient: 'vertical',
                left: 'left',
                data: ['','','','','']
            },
            series : [
                {
                    name: '',
                    type: 'pie',
                    radius : '55%',
                    center: ['50%', '55%'],
                    data:[
                        {value:12, name:''},
                        {value:15, name:''},
                        {value:20, name:''},
                        {value:20, name:''},
                        {value:40, name:''}
                    ],
                    itemStyle: {
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        };
    };
 
    jumpMyPerformance=()=>{
      createHashHistory().push("/home/jumpMyPerformance"); 
    } 
    render() {
        console.log(typeof(walden));
        var themeString =JSON.stringify(walden)
        Echarts.registerTheme('walden', walden);
        return (
            <div className="me">
               <div  style={{ width: '100%', height:( Global.pagesHeight / 2)-30  + 'px' }}>
                  <Row>
                        <Col span={12}>
                            <div className="jihua keshi">
                            <Row>
                                <Col span={12} className="over">
                                    <h4><Icon type="crown" />120h</h4>
                                </Col>
                                <Col span={12}>

                                  <h5>
                                    
                                 </h5>
                                 <div className="yiwanc">
                                    108h 
                                 </div>
                                 <div className="weiwanc">
                                    12h 
                                 </div>
                                </Col>
                            </Row>
                                 
                                  
                                 
                            </div>
                        </Col>
                        <Col span={12}>
                           <div className=" keshi"  style={{paddingLeft:"30px",paddingTop:20,border:"1px #ccc solid"}}>
                           <ReactEcharts
                            option={this.getOption()}
                            notMerge={true}
                            lazyUpdate={true}
                            // onEvents={onEvents}
                            theme={'walden'}
                            style={{ width: '100%', height: Global.pagesHeight / 2 -80+ 'px' }}
                        />
                           </div>
                        </Col>
                    </Row>
               </div>
               <div  style={{ width: '100%', height:( Global.pagesHeight / 2)  + 'px' }}>
                  <Row>
                        <Col span={16}>
                          <Table columns={columns} scroll={{x: true, y: ( Global.pagesHeight / 2-50) } } size="small"  bordered dataSource={data} />
                        </Col>
                       
                        <Col span={8} style={{paddingLeft:"20px",paddingRight:10}}>
                            <div  >
                            <Card title="" extra={<a href="#"></a>} style={{ width: 300 }}>
                                <p> 
                                    <Button style={{marginBottom:"10px"}} block></Button>
                                  
                                    <Button style={{marginBottom:"10px"}} type="dashed" block>
                                      
                                    </Button>  
                                    <Button style={{marginBottom:"10px"}} type="danger" block>
                                       
                                    </Button>  
                                    <Button style={{marginBottom:"10px"}} type="link" onClick={this.jumpMyPerformance} block>
                                       
                                    </Button>
                                  </p>
                               
                            </Card>
                            </div>
                        </Col>
                       
                    </Row>
               </div>
               
            </div>
        )
    }
}

export default Me;