import React, { Component } from 'react';
import './editAntd.scss';
import './style.scss';
 import {Table,Tag,Divider,Button,Icon} from 'antd';
 const columns = [
    {

        title: '',
        align:'center',
        width:70, 
        render:(text,record,index)=>`${index+1}`
        
        },{
      title: '',
      dataIndex: 'name',
      key: 'name',
      align:'center',
      render: text =>  text ,
    },
    {
      title: '',
      dataIndex: 'teacher',
      key: 'teacher',
      align:'center',
    },
    {
      title: '',
      dataIndex: 'address',
      key: 'address',
      align:'center',
    },
    {
      title: '',
      key: 'tags',
      align:'center',
      dataIndex: 'tags',
      render: tags => (
        <span>
          {tags.map(tag => {
            let color = tag.length > 5 ? 'geekblue' : 'green';
            if (tag === 'loser') {
              color = 'volcano';
            }
            return (
              <Tag color={color} key={tag}>
                {tag.toUpperCase()}
              </Tag>
            );
          })}
        </span>
      ),
    },
    {
      title: '',
      dataIndex: 'action',
      align:'center',
      key: 'action',
      render: (text, record) => {
        var color,con ;
        if(text==0){
          color="orange";
          con=""
        }else if(text==1){
            color="green";
            con=""
        }else {
            color="red";
            con=""
        }
        return (
            <span style={{color}}>{con}</span>
        )
      },
    },
    {
      title: '',
      dataIndex: 'isClick',
      align:'center',
      key: 'isClick', 
      render: (text, record) => {
        var bom ;
        
        if(text){
            return (
              <span><Icon type="check" style={{color:'green'}} /><span style={{color:'green'}}></span> </span>  
            );
        }else {
          return (
             <Button size="small">
                <Icon type="form" /> 
             </Button>
          )  
        }

        
      }
    }
  ];
  
  const data = [
    {
      key: '1',
      name: '',
      teacher: "",
      action:"1",
      address: '',
      tags: ['', ''],
      isClick:1,
    },
    {
      key: '2',
      name: '?',
      teacher: '',
      action:"1",
      address: '',
      tags: [''],
      isClick:1,
    },
    {
      key: '3',
      name: ' ',
      teacher: '',
      action:"0",
      address: '',
      tags: [''],
      isClick:0,
    },
    {
      key: '4',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: ['', ''],
      isClick:0,
    },
    {
      key: '5',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:1,
    },
    {
      key: '6',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:1,
    },
    {
      key: '7',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:1,
    },
    {
      key: '8',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:1,
    },
    {
      key: '9',
      name: ' ',
      teacher: '',
      action:"2",
      address: '',
      tags: [''],
      isClick:1,
    },
     
  ];
class Test extends Component {
    constructor() {
        super();

        this.state = {
          
        }
    }
    
 
    render() {
        return (
            <div className="test">
               <Table columns={columns}  size="small"  bordered dataSource={data} />
            </div>
        )
    }
}

export default Test;